import inputSearch from '@/components/p-inputSearch/inputSearch'
import combox from '@/components/uni-combox/uni-combox'
import interfaces from '@/utils/interfaces'
import uUpload from '@/components/u-upload/u-upload'
import LbPicker from '@/components/lb-picker'
export default {
	components: {
		inputSearch,
		uUpload,
		LbPicker,
	},
	data() {
		return {
			uploadState: {
				COMPLETE: 0,
				UNFINISHED: 1,
				SUCCESS: 2,
				ERROR: 3,
				files: {}
			},
			publishData: {
				needId: "", //需求id
				type: "", //类型1 发布采购 2 发布销售 3 委托销售	
				cate_id: '', //品类id
				brand_id: '', //品牌id
				title: '', //标题
				desc: '', //描述
				brand: '', //品牌选择
				otherBrand: '', //其他品牌,非必选
				country: '', //出口国,非必填
				supplierPrice: '', //供应商价格,仅发布销售可用
				price: '', //市场价	
				num: '', //数量
				unit_cate_id: '1', //单位id
				qualification: '', //资质
				use_way: '', //用途
				thumbnail: '', //上传图片-产品
				deadtime: '', //截止时间
				images: [], //产品轮播图
				info: [], //产品详情图
				service_id: ''
			},
			entrustList: [{
					value: '3',
					name: '销售',
				},
				{
					value: '4',
					name: '采购',
				},
			],
			dataSource: [], //出口国家
			inputValue: '',
			brandvalue: '', //当前选择品牌
			selectbrand: [], //选择品牌
			selectUnit: [], //选择单位
			curUnit: '请选择', //当前选择单位
			action: interfaces.getUploadData,
			customBtn: false,
			isCheckAll: false,
			current: 0, //选择委托方式
			filesArr: [],
			lists: [], // 组件内部的文件列表
			Listids: [], //选择增值服务
			mainImage: [], //宝贝主图
			swipeImage: [], //轮播图
			productList: [], //商品详情
			useway: '',
			useItems: [{
					value: '1',
					name: '民用',
					//checked:'',
				},
				{
					value: '2',
					name: '医用',
					//checked:'',
				},
			],
			serviceList: [{
					id: '1',
					name: '置顶',
					price: '30',
				},
				{
					id: '2',
					name: '加急',
					price: '30'
				},
			],
			qualtitems: [],
			brandProps: {
				label: 'cn_name', //下拉值
				value: 'cn_name', //显示值
				brandid: 'id'
			},
			UnitProps: {
				label: 'cn_name', //下拉值
				value: 'cn_name', //显示值
				unitid: 'id'
			}
		}
	},
	onLoad(option) {
		//this.publishData.type = option.type;
		this.publishData.cate_id = option.cate_id;
		this.publishData.needId = option.id;
		this.initData(); //初始化数据
		this.getProdcutData();
		this.countryData(); //出口国
	},
	methods: {
		//获取默认发布信息
		getProdcutData() {
			this.request({
				url: interfaces.getInfoData,
				dataType: "JSON",
				method: 'POST', //请求方式
				data: {
					data: {
						needs_id: this.publishData.needId
					}
				},
				success: (res) => {
					if (res.code == 200) {
						var item = res.data;
						for (let i = 0; i < this.entrustList.length; i++) {
							if (item.type==this.entrustList[i].value) {
								this.current = i;
								break;
							}	
						}
						this.publishData.type = item.type //类型
						this.publishData.title = item.title //标题
						this.publishData.desc = item.desc //描述
						this.publishData.otherBrand = item.other_brand //其他品牌
						this.publishData.deadtime = item.dead_time.substring(0, 10) //截止时间
						this.publishData.num = item.num //数量
						this.publishData.price = item.price //价格
						this.publishData.supplier_price = item.supplier_price //供应商价格
						this.brandvalue = item.product_brand_cnname, //品牌
						this.publishData.brand_id = item.brand_id;
						this.curUnit = item.unit_category_cnname, //单位
						this.publishData.unit_cate_id = item.unit_cate_id;
						this.inputValue = item.exit_country,
						this.exit_country = item.exit_country; //出口国
						for (let i = 0; i < this.useItems.length; i++) { //用途
							if (item.use_way == this.useItems[i].value) {
								this.useway = i;
							}
							this.publishData.use_way = item.use_way
						}
						let chekbox = item.qualification //资质		
						let chekboxArr = chekbox != null && chekbox.length ? chekbox.split(',') : ''
						for (let i = 0; i < chekboxArr.length; i++) {
							for (let j = 0; j < this.qualtitems.length; j++) {
								if (chekboxArr[i] == this.qualtitems[j].id) {
									const item = this.qualtitems[j];
									this.$set(item, 'checked', true)
								}
							}
						}
						 this.publishData.qualification=chekbox;
						let thumb = {
							url: item.thumbnail
						}
						this.mainImage.push(thumb) //主图
						this.publishData.thumbnail = item.thumbnail
						let swImages = item.images;
						let swiperArr = swImages != null && swImages.length ? swImages.split(',') : ''
						for (let i = 0; i < swiperArr.length; i++) { //轮播图
							this.swipeImage.push({
								url: swiperArr[i]
							})
						}
						this.publishData.images = swImages
						let infoImage = item.info;
						let infoArr = infoImage != null && infoImage.length ? infoImage.split(',') : ''
						for (let i = 0; i < infoArr.length; i++) {
							this.productList.push({
								url: infoArr[i]
							}) //详情图

						}
						this.publishData.info = infoImage
						let service = item.service_id; //服务选择
						let serviceArr = service != null && service.length ? service.split(',') : ''
						for (let i = 0; i < serviceArr.length; i++) {
						for (let j = 0; j < this.serviceList.length; j++) {
							if (serviceArr[i] == this.serviceList[j].id) {
								this.Listids.push(this.serviceList[i].id);
							}
						}
						}
						this.publishData.service_id = service
					}
				}
			})
		},
		checkUploadFiles() {
			let finished = true;
			let uploadState = this.uploadState;
			let files = this.uploadState.files;
			for (let fieldName in files) {
				if (files[fieldName] == uploadState.UNFINISHED || files[fieldName] == uploadState.EEROR) {
					//存在未上传成功或未上传完毕的图片，策略后继可以按需求修改
					finished = false;
					break;
				}
			}
			return finished;
		},
		uploadHandler(args, handlerName, fieldName) {
			let argsMerge = [];
			for (let i = 0; i < args.length; i++) {
				argsMerge.push(args[i]);
			}
			argsMerge.push(fieldName);
			this[handlerName].apply(this, argsMerge);
		},
		onProgress(res, index, lists, fieldName) {
			this.uploadState.files[fieldName] = this.uploadState.UNFINISHED;
		},
		onSuccess(res, index, lists, fieldName) { //fieldName 服务器接收该图片的字段名
			res = JSON.parse(res);
			this.uploadState.files[fieldName] = this.uploadState.SUCCESS;
			//保存已上传完的文件，用于单个上传组件多图上传时，不同的上传组件应使用不同的数组保存
			//this.fileList.push({url:res.data.img_url});
			this.publishData[fieldName] = res.data.img_url;
			var imglist = ["images", "info"];
			if (imglist.includes(fieldName)) {
				var listimg = [];
				for (var i = 0; i < lists.length; i++) {
					if (typeof lists[i].response == 'string') {
						var data = JSON.parse(lists[i].response);
						listimg.push(data.data.img_url);
					}
				}
				this.publishData[fieldName] = listimg.join(",");
			}
		},
		onChange(res, index, lists, fieldName) {
			this.uploadState.files[fieldName] = this.uploadState.COMPLETE;
		},
		onError(err, index, lists, fieldName) {
			this.uploadState.files[fieldName] = this.uploadState.EEROR;
		},
		onRemove(index, lists, fieldName) {
			// this.uploadState.files[fieldName] = undefined;
			// this.publishData[fieldName] = '';
			var imglist = ["images", "info"];
			if (imglist.includes(fieldName)) {
				this.publishData[fieldName].splice(index, 1)
			} else {
				this.uploadState.files[fieldName] = undefined;
				this.publishData[fieldName] = '';
			}
		},

		typeChange: function(evt) { //获取type
			for (let i = 0; i < this.entrustList.length; i++) {
				if (this.entrustList[i].value === evt.target.value) {
					this.current = i;
					break;
				}
			}
			this.publishData.type = evt.target.value; //获取type
		},
		initData() {
			//品牌种类
			this.request({
				url: interfaces.getBrandData,
				dataType: "JSON",
				method: 'POST', //请求方式
				data: {
					data: {
						cate_id: this.publishData.cate_id
					}
				},
				success: (res) => {
					//debugger
					this.selectbrand = res.data.length?res.data:[];
				}
			});
			//获取资质种类分类列表
			this.request({
				url: interfaces.getqualtData,
				dataType: "JSON",
				method: 'POST', //请求方式
				success: ((res) => {
					this.qualtitems = res.data;
				})
			});
			//获取单位
			this.request({
				url: interfaces.getUnitData,
				dataType: "JSON",
				method: 'POST', //请求方式
				success: ((res) => {
					this.selectUnit = res.data;
				})
			});
		},
		//出口国
		countryData() {
			this.request({
				url: interfaces.getCountryData,
				dataType: "JSON",
				method: 'POST', //请求方式
				success: ((res) => {
					this.dataSource = res.data;
				})
			});
		},
		handleChange(data) { //获取出口国
			this.exit_country = data;

		},
		handleTap(name) { //picker弹出
			this.$refs[name].show()
		},
		brandChange(item) { //品牌id 
			this.publishData.brand_id = item.item.id;
		},
		Unithange(item) { //单位id

			this.publishData.unit_cate_id = item.item.id;
		},
		radioChange: function(e) { //用途选择
			this.publishData.use_way = e.detail.value;
		},
		qualtChange: function(e) { //资质证书
			var items = this.qualtitems,
				values = e.detail.value;
			for (var i = 0, lenI = items.length; i < lenI; ++i) {
				const item = items[i]
				if (values.indexOf(item.value) >= 0) {
					this.$set(item, 'checked', true)
				} else {
					this.$set(item, 'checked', false)
				}
			}
			var data = values.join(',');
			this.publishData.qualification = data
		},
		checkboxChange: function(id) { //选择增值服务
			var ids = this.Listids.indexOf(id);
			if (ids >= 0) {
				//如果包含了该ID，则删除（单选按钮由选中变为非选中状态）
				this.Listids.splice(ids, 1);
			} else {
				//选中该按钮
				this.Listids.push(id);
			}
			var serviceId = this.Listids.join(',');
			this.publishData.service_id = serviceId
		},
		successlist: function(data, index, lists) {
		},
		publishSubmit() {
			//修改当前的发布信息
			let params = {
				data: {
					id: this.publishData.needId,
					type: this.publishData.type,
					cate_id: this.publishData.cate_id,
					title: this.publishData.title,
					desc: this.publishData.desc,
					thumbnail: this.publishData.thumbnail,
					other_brand: this.publishData.otherBrand,
					brand_id: this.publishData.brand_id,
					num: this.publishData.num,
					exit_country: this.exit_country,
					price: this.publishData.price,
					dead_time: this.publishData.deadtime,
					use_way: this.publishData.use_way,
					qualification: this.publishData.qualification,
					unit_cate_id: this.publishData.unit_cate_id,
					service_id: this.publishData.service_id,
					images: this.publishData.images,
					supplier_price: this.publishData.supplier_price,
					info: this.publishData.info,
				}
			}
			console.log('584', params);
			this.request({
				url: interfaces.getModifyData,
				dataType: "JSON",
				method: 'POST', //请求方式
				data: params,
				success: (res) => {
					if (res.code == 200) {
						uni.showToast({
							title: '修改成功，请前往首页查看',
							icon: "none"
						})
						setTimeout(() => {
							uni.switchTab({
								url: "/pages/index/index"
							})
						}, 1000);
					} else {
						uni.showToast({
							title: res.message,
							icon: "none"
						});
					}
				}
			})
		},
	}
}
